import React, { useEffect, useState } from 'react'
import Popup from '../Popup'
import './style.css'
import CookieConsent, { Cookies } from "react-cookie-consent";
import * as logofull from '../../../assets/DULT_logo.png'
import { useColourObjectStore } from '../../../store/ColourStore';
import { useActivePopup } from '../../../store/ActivePopup';
import { useActiveScreen } from '../../../store/ActiveScreen';
import HeaderComponent from '../HeaderComponent';
import FooterComponent from '../FooterComponent';
import McInput from '../McInput';
import { generateOTP, Sociallogin } from 'utils/actions';
import { useHistory } from 'react-router-dom';

const Cookie = (props: any) => {
  const { colourObj } = useColourObjectStore();
  const { setActivePopup } = useActivePopup();
  const { ActiveScreen, setActiveScreen } = useActiveScreen();

  const [cookie_popup, setcookie_popup] = useState(navigator.cookieEnabled)
  const [EmailLoginPopup, setEmailLoginPopup] = useState(false);
  const [aniout, setaniout] = useState(false);

  const [otp_popup, setotp_popup] = useState(true);

  const [email, setemail] = useState('');
  const [otp, setotp] = useState('');

  const history = useHistory();

  useEffect(() => {
    console.log('Cookies Enabled ?', cookie_popup, props.localstorage);
    lsTest()
  }, [])


  const lsTest = () => {
    // var test = 'test';
    // try {
    //   localStorage.setItem(test, test);
    //   localStorage.removeItem(test);

    //   console.log('PERMISSION : :', true);
    // } catch (e) {
    //   console.log('PERMISSION : :', false);
    // }
  }

  const fade_in = (props: any) => {
    let result = (aniout ? (props + " " + "fade_out") : (props + " " + "fade_in"))
    return result
  }

  const loginCallback = async (data: any, errorresponse: any) => {
    if (data.status === 200) {
      setotp_popup(false)
    } else {
      console.log('error ' + JSON.stringify(data));
      console.log('error ' + JSON.stringify(errorresponse));
    };
  }

  const loginCallback_otp = async (data: any, errorresponse: any) => {
    if (data.status === 200) {
      // console.log('user email data ', data);
      localStorage.setItem('AuthToken', JSON.stringify(data.data.results.token));
      localStorage.setItem('UserDetails', JSON.stringify(data.data.results.user_details));
      if (data.data.results.user_details.user_type === "SUPER ADMIN") {
        history.push('/dashboard')
        props.googlecookies("")
        setcookie_popup(false)
      } else {
        setActiveScreen("Home")
        setEmailLoginPopup(false)
        setcookie_popup(false)
        props.googlecookies("")
      }
    } else {
      console.log('error ' + JSON.stringify(data));
      console.log('error ' + JSON.stringify(errorresponse));
    };
  }


  const handleLogin = () => {
    let a = document.getElementById("input_emailtextbox").value
    let data = {
      "email_or_phone": a,
    }
    // console.log(a, b);
    generateOTP(loginCallback, data)
  };

  const OTPvalidate = () => {
    let data = {
      email: email,
      auth_provider: "otp",
      otp: document.getElementById("input_otptextbox").value
    }
    Sociallogin(loginCallback_otp, data)
  };

  return (
    <div style={{ zIndex: 999999 }}>

      {(props.localstorage_permission) === "disable" &&
        < Popup
          classname={"cookie"}
          title={"ENABLE BROWSER COOKIES"}
          actionable={false}
          popup_body={
            <div >
              <div className='cookie_top_container'>
                <img className='cookie_menu_logo' src={logofull} />
              </div>
              <div className="desc">
                <div>Oops, look's like your cookies are Not Enabled..</div><br />
                <div>We serve cookies on this site to analyze traffic, remember your preferences, and optimize your experience.</div><br />
                <div><u>Please Enable cookies in your browser settings.</u></div><br />
                {"Settings > Privacy & Security > Cookies and other site data > Allow all cookies"}<br />
                And then Refresh the page.
              </div>
            </div>
          }
        />
      }

      {!cookie_popup || props.googlecookies === "idpiframe_initialization_failed" &&
        <Popup
          classname={"cookie"}
          title={"ENABLE BROWSER COOKIES"}
          actionable={false}
          popup_body={
            <div >
              <div className='cookie_top_container'>
                <img className='cookie_menu_logo' src={logofull} />
              </div>

              <div className="desc">
                <div>Oops, look's like your cookies are Not Enabled..</div><br />
                <div>We serve cookies on this site to analyze traffic, remember your preferences, and optimize your experience.</div><br />
                <div><u>Please Enable cookies in your browser settings.</u></div><br />
                {"Settings > Privacy & Security > Cookies and other site data > Allow all cookies"}<br />
                And then Refresh the page.
              </div>

              {props.googlecookies === "idpiframe_initialization_failed" &&
                <div className="cookie_email_login_button_container">
                  <div className="desc">OR</div><br />
                  <div className="desc"><u>Login with your Email ID</u></div><br />
                  <button className="cookie_email_login_button shadow" style={{ color: colourObj.color_m3, backgroundColor: colourObj.color_m1, borderColor: colourObj.color_m3 }}
                    onClick={() => {
                      setEmailLoginPopup(true)
                      setcookie_popup(false)
                    }}>LOGIN</button>
                </div>
              }

            </div>
          }
        />
      }

      {EmailLoginPopup &&

        <Popup
          actionable={false}
          popup_body={
            <div className={fade_in("login_page")}>

              <HeaderComponent screen={"login"} />

              <div className={fade_in("login_wrapper")}>

                <div className='login_top_container'>
                  <img className='login_menu_logo' src={logofull} />
                </div>

                <div className='loginTitle_Text'>EMAIL LOGIN</div>

                <div className="login_form_wrapper">

                  <div className="login_form_bg"></div>

                  <div className='loginDescription_Text' style={{ textDecorationColor: colourObj.color_m2 }}>Login or Sign Up with Email :</div>

                  <div className=" login_popupformcontainer1">

                    {otp_popup ?
                      <div className="SMlogin_button_container">
                        <McInput
                          label={"EMAIL ID"}
                          id="input_emailtextbox"
                          name={`data.email`}
                          inputtype="email"
                          type="text"
                          min_length="3"
                          required={true}
                          value={email}
                          onchange={setemail}
                        />
                        <div className="cookie_email_login_button_container">
                          <button className="cookie_email_login_button shadow" style={{ color: colourObj.color_m3, backgroundColor: colourObj.color_m1, borderColor: colourObj.color_m3 }}
                            onClick={() => {
                              handleLogin()
                            }}>GET OTP</button>
                        </div>
                      </div>
                      :
                      <>
                        <div className="SMlogin_button_container">
                          <McInput
                            label={"OTP"}
                            id="input_otptextbox"
                            name={`data.otp`}
                            inputtype="Text"
                            type="password"
                            min_length="3"
                            required={true}
                            value={otp}
                            onchange={setotp}
                          />
                          <div className="cookie_email_login_button_container">
                            <button className="cookie_email_login_button shadow" style={{ color: colourObj.color_m3, backgroundColor: colourObj.color_m1, borderColor: colourObj.color_m3 }}
                              onClick={() => {
                                setActivePopup("")
                                OTPvalidate()
                              }}>LOGIN</button>
                          </div>
                        </div>
                        <div className="clickme"
                          onClick={() => {
                            setemail('')
                            setotp('')
                            setotp_popup(true)
                          }}><u>Click to Re-enter your Email ID</u></div>
                      </>
                    }


                  </div>
                </div>

              </div>


              <FooterComponent overflow="hidden" />

            </div>
          }
        />

      }
    </div>
  )
}

export default Cookie