import React, { useEffect } from 'react'
import { useColourObjectStore } from '../../../store/ColourStore';
import './style.css'

const FooterComponent = (props: any) => {

  const { screen, footer_body, aniout } = props;

  const { colourObj } = useColourObjectStore();

  useEffect(() => {
    // console.log("FC", aniout, props.overflow);
    // console.log("m1: ",colourObj.color_m1);
    // console.log("m2: ",colourObj.color_m2);
    // console.log("m3: ",colourObj.color_m3);
    // console.log("s1: ",colourObj.color_s1);
    // console.log("s2: ",colourObj.color_s2);
    // console.log("s3: ",colourObj.color_s3);
    // console.log("t1: ",colourObj.color_t1);
    // console.log("t2: ",colourObj.color_t2);
    // console.log("t3: ",colourObj.color_t3);  
  }, [aniout])

  const slide_down = (props: any) => {
    let result = (aniout ? (props + " " + "slide_down123") : (props + " " + "slide_up456"))
    return result
  }

  const t_back = (props: any) => {
    let result = (aniout ? (props + " " + "transform_circle_for_footer_videoback") : (props))
    return result
  }

  return (
    <div className={(screen?.includes("VIDEO")) ? slide_down("footer2") : "footer1"} style={{ overflow: props.overflow }}>
      <div className={screen?.includes("VIDEO") ? t_back("footer_bg_circle2") : "footer_bg_circle1"} style={{ backgroundColor: colourObj.color_m1 }} />
      {footer_body}
      {(screen === "Splash") && <div className="poweredby_text">Powered by Media Cannibal</div>}
    </div>
  )
}

export default FooterComponent